@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: 0;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: rgb(22, 22, 22);
    --color-bg-variant: #3a3e4d ;
    --color-primary: #ff908d;
    --color-primary-variant: #ff0f09;
    --color-white: rgb(217, 217, 217);
    --color-light: rgb(189, 187, 187);

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

    --transition: all 400ms ease;
    /* --transition-icon: all 2s ease-in-out; */
}

html{ 
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display:none;
    
}

body {
    font-family: 'Montserrat', sans-serif;
    background-color: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
}

/* =========GENERAL STYLES========= */

.container {
     width: var(--container-width-lg);
     margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h2, section > h5{
    text-align: center;
    color: var(--color-light);
} 

section > h2 {
    color: var(--color-primary );
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
     color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: .75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary)
   
}

.btn:hover {
    background-color: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
    transition: (var(--transition));
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover
}

/* TABLETS MEDIA QUERIES */

@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    section {
        margin-top:6rem;
    }
}

/* PHONE MEDIA QUERIES */

@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }

    section > h2 {
        margin-top: 2rem;
    }
}