.about_container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about_me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}

.about_me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: scale(.85);
    transition: var(--transition);
}

.about_me-image:hover {
    transform: scale(1);
}

.about_cards{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
}

.about_card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about_card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about_icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
    /* transition: var(--transition); */
}

.about_card:hover .about_icon {
    transform: rotateY(0deg);
    animation: iconAnimation infinite 3s ease;
}

@keyframes iconAnimation {
    25% {
        color: rgb(205, 43, 43);
        transform: rotateY(180deg);
    }

    50% {
        color: rgb(6, 188, 6);
        transform: rotateY(360deg);
    }

    75% {
        color: rgb(6, 134, 193);
        transform: rotateY(180deg);
    }

    100% {
        color: rgb(230, 230, 47);
        transform: rotateY(360deg);
    }
}

.about_card h5 {
    font-size: 1rem;
}

.about_card small {
    font-size: .8rem;
    color: var(--color-light);
}

.about_content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

/* TABLETS MEDIA QUERIES */

@media screen and (max-width: 1024px) {
    .about_container {
        grid-template-columns: 1fr;
        gap: 0;
    }
    
    .about_me {
        width: 50%;
        margin: 2rem auto 4rem;
    }
    
    .about_content p {
        margin: 1rem 0 1.5rem;
    }
}

/* PHONE MEDIA QUERIES */

@media screen and (max-width: 600px) {
    .about_me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about_cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about_content {
        text-align: center;
    }

    .about_content p {
        margin: 1.5rem 0;
    }
}